
.homeMovie{position:relative;width:100%;height:100%;background:#1C2635;height:4.4rem
}
.homeMovie .backToHome{background:url("./h_btn_back.png") no-repeat center center;background-size:1.2rem 1.5rem;width:3rem;height:100%;position:absolute;left:0;top:0
}
.homeMovie .hotMovie_text_cnt{width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;margin:0 auto;-ms-flex-align:center;align-items:center
}
.homeMovie .hotMovie_text_cnt>div{background:#0D121A;border-radius:4rem
}
.homeMovie .hotMovie_text_cnt a{text-align:center;display:inline-block;font-size:1.3rem;line-height:1;color:#fff;padding:.7rem 2.8rem;border-radius:4rem;background:#0D121A;border:1px solid #0D121A
}
.homeMovie .hotMovie_text_cnt a.active{background:#1c2635;border-color:#0D121A
}



