
.tab{width:100%;display:-ms-flexbox;display:flex;background:#1C2635
}
.tab .tab-item{-ms-flex:1;flex:1;text-align:center
}
.tab .tab-item a{height:4.4rem;line-height:4.4rem;display:block;font-size:1.3rem;color:#bbb
}
.tab .tab-item .homePic,.tab .tab-item .login{padding:0 .85333rem
}
.tab .tab-item .homePic i,.tab .tab-item .login i{vertical-align:middle;display:inline-block;width:5.6rem;height:1.8rem;background:url("logo_mtime.png") no-repeat center center;background-size:contain
}
.tab .tab-item .login i{width:1.4rem;height:1.8rem;background:url("ico_my.png") no-repeat center center;background-size:cover
}
.tab .tab-item .shop{position:relative
}
.tab .tab-item .shop em{position:absolute;width:2.7rem;height:1.1rem;line-height:1.1rem;background:-webkit-linear-gradient(top, #ef4545, #d80808);right:-1.2rem;top:.59733rem;font-size:1.024rem;font-style:normal;border-radius:1rem;overflow:hidden;color:#fff
}
.tab.foot .tab-item{border-top:1px solid #F6F6F6;background:#fff
}
.tab.foot .tab-item a{color:#0074C5
}



