
.movie_pic{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap
}
.movie_pic .movie_item{width:25%
}
.movie_pic .movie_item .title{padding:.5rem 0 0;margin-bottom:1rem;height:3.8rem;width:100%;text-align:center;font-size:1.4rem;line-height:1.2;color:#333
}
.movie_pic .movie_item .movie_item_pic{position:relative;width:6.5rem;height:9.8rem;text-align:center
}
.movie_pic .movie_item .movie_item_pic .img{width:100%;height:100%;border:1px solid #ddd;margin:0 auto
}
.movie_pic .movie_item .movie_item_pic .score{position:absolute;right:.3rem;top:.3rem;width:2rem;height:1.8rem;background:#659d0e;color:#fff;line-height:1.8rem;text-align:center
}



