
.nowMovie .nowMovie_item{padding:1.5rem;display:-ms-flexbox;display:flex;border-bottom:1px solid #D8D8D8
}
.nowMovie .nowMovie_item img{width:6.5rem;height:9.8rem;margin-right:1.5rem
}
.nowMovie .nowMovie_item .middle{width:100%
}
.nowMovie .nowMovie_item .middle .middle_desc{min-height:6.7rem
}
.nowMovie .nowMovie_item .middle .middle_desc h1 .title{font-size:1.5rem;vertical-align:middle
}
.nowMovie .nowMovie_item .middle .middle_desc h1 .score{margin-left:.5rem;font-size:1.1rem;background:#659d0e;width:2em;height:1.8em;color:#fff;display:inline-block;line-height:1.8rem;text-align:center
}
.nowMovie .nowMovie_item .middle .middle_desc .desc{color:#659d0e;padding-top:.5rem
}
.nowMovie .nowMovie_item .middle .middle_desc .desc i{display:inline-block;margin:-0.5rem .3rem 0 0;width:1.1rem;height:.8rem;background:url("./i_dot_01.png") no-repeat 0 0;background-size:cover
}
.nowMovie .nowMovie_item .middle .middle_desc .desc span{width:18rem;display:inline-block;vertical-align:middle;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;font-size:1.3em;line-height:1.65
}
.nowMovie .nowMovie_item .middle .middle_desc>div .movie_01{margin-top:.4rem;display:inline-block;width:2rem;height:1.5rem;font-style:normal;background:url("./ia_cine_05.png") no-repeat 0 0;background-size:cover
}
.nowMovie .nowMovie_item .middle .middle_desc>div .movie_01.movie_01_tv{width:4.4rem;background:url("./ia_cine_15.png") no-repeat;background-size:cover
}
.nowMovie .nowMovie_item .middle .cinema{font-size:1.2rem;color:#999;line-height:2.8rem
}
.nowMovie .nowMovie_item .middle .cinema .purchase{font-size:1.4rem;margin-left:1rem;min-width:7.4rem;float:right;background:#ff8600;color:#fff;border:1px solid #ff8600;display:inline-block;border-radius:40rem;text-align:center
}



