
.login_page{background:#F6F6F6;height:52.39467rem
}
.login_page .login_box{padding:0 1.5rem;background:#fff;box-shadow:0 3px 3px rgba(51,51,51,0.1)
}
.login_page .login_box .login_user,.login_page .login_box .login_psd{background:#fff;height:3rem;display:-ms-flexbox;display:flex;border-bottom:1px solid #d8d8d8;padding:1rem 0;-ms-flex-align:center;align-items:center
}
.login_page .login_box .login_user .login_user_img,.login_page .login_box .login_user .login_psd_img,.login_page .login_box .login_psd .login_user_img,.login_page .login_box .login_psd .login_psd_img{display:inline-block;width:2.6rem;height:2.3rem;background:url("./i_user.png") no-repeat;background-size:cover;margin-right:.8rem
