
.search{display:-ms-flexbox;display:flex;padding:.8rem 1.5rem .75rem;background:#f6f6f6;-ms-flex-align:center;align-items:center;border-bottom:1px solid #d8d8d8
}
.search .search_choose{width:6rem;position:relative
}
.search .search_choose span{font-size:1.6em
}
.search .search_choose i{position:absolute;right:.42667rem;top:50%;display:inline-block;width:.9rem;height:.5rem;background:url("i_city.png") no-repeat;background-size:cover
}
.search .search_box{width:100%;position:relative;padding-right:.85333rem
}
.search .search_box .search_text{width:80%;height:3rem;line-height:1.4rem;font-size:1.4rem;padding:0 1.8rem 0 2rem;border-radius:.5rem;outline:none;background:url("search_ico_01.png") no-repeat 0.5rem center;background-color:#fff;background-size:1.3rem 1.3rem
}
.search .search_box .search_clear{position:absolute;right:1.4rem;top:28%;display:inline-block;width:2.2rem;height:2.2rem;background:url("search_clear.png") no-repeat;background-size:1.3rem 1.3rem
}



